@import url(https://fonts.googleapis.com/css2?family=Kalam:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Architects+Daughter&display=swap);
html {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}

#root {
  width: 100%;
  height: 100%;
}
* {
  box-sizing: border-box;
}
body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: "Kalam", cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

button {
  font-family: "Architects Daughter", cursive;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  width: 100%;
  min-height: 100%;
  box-sizing: border-box;
  padding-bottom: 5px;
}

.fixedHeight {
  height: 100%;
}

.items {
  width: 300px;
  height: 450px;
  box-sizing: border-box;
  padding: 15px;
  background-color: white;
  border-radius: 20px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px;
}

.items img {
  width: 170px;
  height: 220px;
}

.imgContainer {
  text-align: center;
}

.items p {
  height: 75px;
}

.items button {
  width: 100%;
  background-color: rgb(0, 0, 0);
  color: white;
  border: none;
  padding: 10px;
  cursor: pointer;
  margin: 5px 0px 0px 0px;
}

.items button:active {
  background-color: rgb(255, 255, 255, 0.1);
  color: black;
}

.itemsContainer {
  width: 80%;
  margin: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  grid-gap: 50px;
  gap: 50px;
  padding: 15px;
}

.loader {
  border: 4px solid #f3f3f3; /* Light grey */
  border-top: 4px solid #000; /* Blue */
  border-radius: 50%;
  width: 40px;
  height: 40px;
  -webkit-animation: spin 2s linear infinite;
          animation: spin 2s linear infinite;
  margin: auto;
}

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.cartItems img {
  min-width: 170px;
  max-width: 170px;
  min-height: 220px;
  max-height: 220px;
}

.cartItems {
  display: flex;
  align-items: center;
  grid-gap: 10px;
  gap: 10px;
  margin-bottom: 25px;
  width: 100%;
  box-sizing: border-box;
}

.cartItems .details button {
  margin: 20px 0px 20px 0px;
  cursor: pointer;
}

#quantity {
  margin: 0px 10px 0px 10px;
}

.cart {
  width: 100%;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 20px;
}

.cartContent {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.cartContent,
.total {
  width: 80%;
  margin: auto;
  box-sizing: border-box;
}

.total {
  border-top: solid 1px black;
  text-align: center;
}

.total button {
  width: 50%;
  padding: 10px;
  background-color: rgba(0, 0, 0, 0.1);
  color: black;
  cursor: pointer;
  font-size: 1rem;
  border: solid 1px black;
  box-shadow: rgba(0, 0, 0, 0.56) 0px 22px 70px 4px;
}

header {
  padding: 20px;
}
.sticky {
  background-color: yellow;
}
.container {
  width: 80%;
  margin: auto;
}
header h1 {
  margin: 0;
  width: -webkit-min-content;
  width: min-content;
  padding: 10px;
}
nav {
  width: 30%;
  box-sizing: border-box;
  float: right;
  position: relative;
  bottom: 70px;
}

nav ul {
  display: flex;
  padding: 0;
  margin: 0;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  box-sizing: border-box;
}

@media screen and (max-width: 768px) {
  header .container {
    display: flex;
    justify-content: center;
  }
  nav {
    position: static;
    width: 100%;
    background-color: white;
    padding: 20px;
  }

  nav ul {
    justify-content: space-around;
  }
  .sticky {
    position: sticky;
    top: 0;
    background-color: white;
    width: 100%;
  }
}

nav li {
  list-style-type: none;
  font-size: 1rem;
  font-weight: bold;
  margin: 0;
  padding: 0;
}

nav img {
  width: 35px;
  height: auto;
}

#itemsNum {
  position: relative;
  bottom: 20px;
  background-color: black;
  color: white;
  display: inline-block;
  font-size: 1rem;
  width: 30px;
  text-align: center;
  border-radius: 50%;
}

.home {
  height: calc(100% - 120px);
  width: 100%;
}

.homeContent {
  width: 80%;
  margin: auto;
  height: 100%;
  padding-bottom: 20px;
  padding-top: 50px;
  box-sizing: border-box;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: left;
}

.homeText {
  width: 40%;
  padding: 10px 15px 10px 15px;
  background-color: rgb(227, 226, 226, 0.4);
  float: right;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px inset,
    rgba(0, 0, 0, 0.3) 0px 18px 36px -18px inset;
  box-sizing: border-box;
}

@media screen and (max-width: 768px) {
  .homeText {
    width: 100%;
  }

  .homeContent {
    background-size: cover;
    background-position: center;
  }
}

.homeText span {
  font-size: 1.5rem;
}

.homeText p {
  font-size: 2rem;
  margin: 0;
}

.homeText button {
  background-color: black;
  color: white;
  padding: 10px;
  cursor: pointer;
  font-size: 1rem;
  width: 150px;
  margin: 20px 0px;
  border: none;
  box-shadow: rgba(0, 0, 0, 0.56) 0px 22px 70px 4px;
  float: right;
}

.homeText button:active {
  background-color: rgb(255, 255, 255, 0.1);
  color: black;
}

